
.card{
    max-height: 25rem;
    width:13rem;
    margin-right: 1.5rem;
    margin-bottom: 1.5rem;
    background-color: rgb( 80, 102, 227);
    overflow: hidden;
    position: relative;
    box-shadow: 0px, 5px, 10px, 0px;
}

.poster img{
    max-width: 15rem;
    max-height: 20rem;
}

.info{
    display: flex;
    justify-content: space-between;
    margin: 0.5rem;
}

.overview{
    transform: translateY(-120%);
    position: absolute;
    top:0;
    bottom:0;
    left:0;
    right:0;
    padding: 1rem;
    overflow: auto;
    margin-top: 3rem;
    border-radius: 1.5rem;
    transition: 0.3s ease-in-out;
}

.card:hover .overview{
    transform: translateY(0);
    background-color: rgb( 80, 102, 227);
}



